@import url('https://fonts.googleapis.com/css?family=Open+Sans|Ubuntu:500');
@import './components/variables';
@import './components/hero';
@import './components/navbar';
@import './components/customers';
@import './components/how-it-works';
@import './components/benefits';
@import './components/contact-form';
@import './components/footer';


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Open Sans', sans-serif;
}

img {
    width: 100%;
}

.container {
    width: 85%;
    margin: 0 auto;
    clear: both;
}

.section__title {
    font-family: 'Ubuntu', sans-serif;
    margin: 3rem;
    font-size: 3rem;
    text-align: center;
    line-height: 4rem;
    &--min-bot-margin {
        margin-bottom: 1rem;
    }
}

.section__subtitle {
    font-size: 1.5rem;
    text-align: center;
    line-height: 2rem;
}

.btn {
    position: relative;
    z-index: 1;
    font-weight: bold;
    border: none;
    border-radius: 15px;
    font-size: 1.25rem;
    padding: 10px;
    &--lg {
        font-size: 1.75rem;
        padding: 1rem 1.75rem;
    }
    &--right {
        margin-top: 10%;
        margin-left: 65%;
    }
    &--yellow {
        background-color: $yellow;
        color: white;
    }
    &--wide {
        width: 100%;
    }
    &--red {
        background-color: $red;
        color: white;
    } 
    &--non-rounded {
        border-radius: 0;
    }
}


@media screen and (min-width: 1405px) {
    .container {
        width: 1200px;
    }
}

@media screen and (max-width: 768px) {
    .btn {
        &--right {
            margin-top: 30%;
        }
    }
}

@media screen and (max-width: 400px) {
    .section__title {
        font-size: 2.5rem;
    }
}